<template>
	<scroll-view scroll-y="true" class="borrow-list" :show-scrollbar="false">
		<view class="tabs">
			<u-tabs :list="list" lineWidth="30" lineColor="#FE542C" 
				:activeStyle="{
					color: '#333',
					transform: 'scale(1.05)'
				}" 
				:inactiveStyle="{
						color: '#606266',
						transform: 'scale(1)'
				}" 
				itemStyle="padding-bottom: 12rpx; height: 34px; width: 40%;"
			>
			</u-tabs>
		</view>
		<view class="u-page">
			<view v-if="indexList.length">
				<view v-for="(item, index) in indexList" :key="index" class="item">
					<view class="title">
						<view class="">2018-11-09 </view>
						<view style="display: flex; align-items: center;" @click="goBorrowDetail">
							<text style="font-size: 25rpx; color: #FE542C;">待还款</text>
							<u-icon name="arrow-right" color="#FE542C"></u-icon>
						</view>
					</view>
					<view class="content">
						<view class="">
							<u--image :showLoading="true" :src="imgSrc" width="68rpx" height="66rpx"></u--image>
						</view>
						<view class="tit">
							白条
						</view>
						<view>
							<u-tag 
								text="分期购物" 
								plain 
								size="mini" 
								color="#999" 
								border-color="rgb(237,237,237)" 
								bg-color="rgb(237,237,237)"
								shape="circle"
							></u-tag>
						</view>
					</view>
				</view>
			</view>
			<view class="" v-else>
				加载中
			</view>
		</view>
	</scroll-view>
</template>

<script>
	export default {
		data() {
			return {
				list: [{
					name: '待还款',
				}, {
					name: '已还款',
				}],
				indexList: [],
				urls: [
					'https://cdn.uviewui.com/uview/album/1.jpg',
					'https://cdn.uviewui.com/uview/album/2.jpg',
					'https://cdn.uviewui.com/uview/album/3.jpg',
					'https://cdn.uviewui.com/uview/album/4.jpg',
					'https://cdn.uviewui.com/uview/album/5.jpg',
					'https://cdn.uviewui.com/uview/album/6.jpg',
					'https://cdn.uviewui.com/uview/album/7.jpg',
					'https://cdn.uviewui.com/uview/album/8.jpg',
					'https://cdn.uviewui.com/uview/album/9.jpg',
					'https://cdn.uviewui.com/uview/album/10.jpg',
				],
				imgSrc: ''
			}
		},

		onLoad() {
			this.loadmore();
			this.imgSrc = require('../../static/baitiao.png')
		},

		methods: {
			// 向下滚动加载
			scrolltolower() {
				this.loadmore()
			},
			
			// 加载数据
			loadmore() {
				for (let i = 0; i < 30; i++) {
					this.indexList.push({
						url: this.urls[uni.$u.random(0, this.urls.length - 1)]
					})
				}
			},
			
			// 待还款
			goBorrowDetail() {
				uni.navigateTo({
					url: '/subPackages/borrowDetail/borrowDetail'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.borrow-list {
		height: 100vh;
		.tabs {
			position: fixed;
			top: 0;
			width: 100%;
			height: 80rpx;
			z-index: 1;
			background-color: #fff;
		}
	}
	.u-page {
		padding: 80rpx 0;
		.item {
			background-color: #fff;
			border-radius: 12rpx;
			padding: 24rpx;
			margin: 18rpx 18rpx 0 28rpx;
		}
		.title {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
	
	.content {
		display: flex;
		align-items: center;
		background-color: #FAFAFA;
		margin-top: 24rpx;
		padding: 40rpx 0 40rpx 34rpx;
		.tit {
			height: 30rpx;
			font-size: 30rpx;
			font-family: PingFangSC, PingFang SC;
			font-weight: 400;
			line-height: 30rpx;
			padding: 0 20rpx 0 22rpx;
		}
	}

</style>